import React, { useState, useEffect } from 'react';
import { auth, storage } from '../firebase';
import { doc, setDoc } from 'firebase/firestore';
import { getFirestore } from 'firebase/firestore';
import { updateProfile } from 'firebase/auth';
import { ref, uploadBytesResumable, getDownloadURL, deleteObject } from 'firebase/storage';

function UserProfile() {
  const [user, setUser] = useState(null);
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [photoURL, setPhotoURL] = useState(null); // Add state for photo URL
  const [previousPhotoRef, setPreviousPhotoRef] = useState(null); // Track previous photo reference

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        setUser(authUser);
        // Split the displayName and set first name and last name
        const splitName = authUser.displayName.split(' ');
        setFirstName(splitName[0] || '');
        setLastName(splitName[1] || '');
        setPhotoURL(authUser.photoURL); // Set the photo URL
      } else {
        setUser(null);
        setFirstName('');
        setLastName('');
        setPhotoURL(null);
      }
    });

    return () => {
      unsubscribe();
    };
  }, []);

  const MAX_FILE_SIZE = 5 * 1024 * 1024; // 5 MB (adjust the limit as needed)

  const handleFileUpload = async (event) => {
    const file = event.target.files[0];

    if (file) {
      if (file.size > MAX_FILE_SIZE) {
        console.error('File size is too big. Please select a smaller file.');
        return;
      }

      const metadata = {
        contentType: file.type,
      };

      // Delete the previous photo if it exists
      if (previousPhotoRef) {
        try {
          await deleteObject(previousPhotoRef);
          console.log('Previous photo deleted successfully');
        } catch (error) {
          console.error('Error deleting previous photo:', error);
        }
      }

      const storageRef = ref(storage, `profileImages/${file.name}`);
      const uploadTask = uploadBytesResumable(storageRef, file, metadata);

      uploadTask.on(
        'state_changed',
        (snapshot) => {
          // Handle upload progress if needed
          const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log(`Upload is ${progress}% done`);
        },
        (error) => {
          // Handle errors during upload
          console.error('Error uploading image:', error);
        },
        () => {
          // Upload completed successfully, get the download URL
          getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
            // Save the download URL to Firestore or use it as needed
            console.log('File available at', downloadURL);

            // Update the user's profile with this URL
            updateProfile(user, { photoURL: downloadURL })
              .then(() => {
                console.log('Profile photo updated successfully');
                setPhotoURL(downloadURL); // Update the state with the new photo URL
                setPreviousPhotoRef(uploadTask.snapshot.ref); // Set the reference of the current photo
              })
              .catch((error) => {
                console.error('Error updating profile photo:', error);
              });
          });
        }
      );
    }
  };


  
  const handleRemoveImage = async () => {
    if (!previousPhotoRef) {
      console.log('No previous photo to delete.');
      return;
    }
  
    try {
      // Delete the previous photo from Firebase Storage
      await deleteObject(previousPhotoRef);
      console.log('Previous photo deleted successfully');
      console.log('Before setting photoURL to null:', photoURL);
      setPhotoURL(null); // Clear the photo URL in the state
      console.log('After setting photoURL to null:', photoURL); // This might still show the previous value due to the asynchronous nature of state updates
      setPreviousPhotoRef(null); // Clear the reference to the previous photo
    } catch (error) {
      console.error('Error deleting previous photo:', error);
    }
  };
  
  

  const updateProfileInfo = async () => {
    if (!user) {
      return;
    }

    // Update the user's display name
    try {
      await updateProfile(user, {
        displayName: `${firstName} ${lastName}`,
      });

      console.log('Profile updated successfully');
    } catch (error) {
      console.error('Error updating profile: ', error);
    }
  };
  

  return (

              <div className="profile-container">
             <div className="profile-intro-section">
  <div className="container">
    <div className="row align-items-center text-white">
      {user && (
        <div className="welcome-text">
          <h1 className="display-3 fw-bold text-capitalize">Welcome, <span className="email-h3">{user.email}</span>!</h1>
        </div>
      )}
    </div>
  </div>

              </div>
         


      <div className="profile-section">
        <div className="container">
          <div className="row">
        
              <div className="my-4">
                <h3>My Profile</h3>
                <hr />
              </div>
              <form className="file-upload">  
                <div className="row mb-4 gx-4">
                  <div className="col-xxl-8 mb-5 mb-xxl-0">
                    <div className="bg-secondary-soft px-4 py-5 rounded">
                      <div className="row g-3">
                        <h4 className="mb-4 mt-0">Contact detail</h4>
                        <div className="col-md-6">
      <label className="form-label">First Name *</label>
      <input
        type="text"
        className="shadow form-control form-control-lg"
        placeholder=""
        aria-label="First name"
        value={firstName}
        onChange={(e) => setFirstName(e.target.value)}
      />
    </div>
    <div className="col-md-6">
      <label className="form-label">Last Name *</label>
      <input
        type="text"
        className="shadow form-control form-control-lg"
        placeholder=""
        aria-label="Last name"
        value={lastName}
        onChange={(e) => setLastName(e.target.value)}
      />
    </div>
                        <div className="col-md-6">
                          <label htmlFor="inputEmail4" className="form-label">Email *</label>
                          <input
  type="email"
  className="shadow form-control form-control-lg"
  id="inputEmail4"
  defaultValue={user ? user.email : ''}
/>                        </div>
                        <div className="col-md-6">
                          <label className="form-label">Mobile number *</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="" aria-label="Phone number" defaultValue="+91 9852 8855 252" />
                        </div>
                      </div>
                    </div>


                    <div className="bg-secondary-soft px-4 py-5 rounded mt-4">
                      <div className="row g-3">
                        <h4 className="mb-4 mt-0">Address</h4>
                        <div className="col-md-6">
                          <label className="form-label">Street</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="Enter Street" />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label">City</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="Enter City" />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label">State</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="Enter State" />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label">Zip Code</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="Zip Code" />
                      </div>
                    </div>
                  </div>

                  </div>
                  <div className="col-xxl-4">
  <div className="bg-secondary-soft px-4 py-5 rounded">
    <div className="row g-3">
    <h4 className="mb-4 mt-0 text-center align-items-center">Upload your profile photo</h4>
      <div className="text-center">
      <div className="square position-relative display-2 mb-3">
  {photoURL ? (
    <img
      src={photoURL}
      alt="User Profile"
      className="position-absolute top-0 start-0 w-100 h-100 rounded"
    />
  ) : (
    <div className="position-absolute top-0 start-0 w-100 h-100 d-flex align-items-center justify-content-center rounded">
      <i class="fas fa-user fa-3x text-secondary"></i>
    </div>
  )}
</div>
        <label htmlFor="customFile" className="btn btn-success-soft">
          Upload
          <input
            type="file"
            accept="image/*"
            id="customFile"
            onChange={handleFileUpload}
            style={{ display: 'none' }}
          />
        </label>
<button type="button" className="btn btn-danger-soft" onClick={handleRemoveImage}>
  Remove
</button>
        <p className="text-muted mt-3 mb-0">
          <span className="me-1">Note:</span>Minimum size 300px x 300px
        </p>
      </div>
    </div>
  </div>
</div>
                  </div>
               

     
                
              
                
              

                <div className="row mb-5 gx-">
                  <div className="col-xxl-6 mb-5 mb-xxl-0">
                    <div className="bg-secondary-soft px-4 py-5 rounded">
                      <div className="row g-3">
                        <h4 className="mb-4 mt-0">Social media detail</h4>
                        <div className="col-md-6">
                          <label className="form-label"><i className="fab fa-fw fa-facebook me-2 text-facebook"></i>Facebook *</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="" aria-label="Facebook" defaultValue="http://www.facebook.com" />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label"><i className="fab fa-fw fa-twitter text-twitter me-2"></i>Twitter *</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="" aria-label="Twitter" defaultValue="http://www.twitter.com" />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label"><i className="fab fa-fw fa-linkedin-in text-linkedin me-2"></i>Linkedin *</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="" aria-label="Linkedin" defaultValue="http://www.linkedin.com" />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label"><i className="fab fa-fw fa-instagram text-instagram me-2"></i>Instagram *</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="" aria-label="Instragram" defaultValue="http://www.instragram.com" />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label"><i className="fas fa-fw fa-basketball-ball text-dribbble me-2"></i>Dribble *</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="" aria-label="Dribble" defaultValue="http://www.dribble.com" />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label"><i className="fab fa-fw fa-pinterest text-pinterest"></i>Pinterest *</label>
                          <input type="text" className="shadow form-control form-control-lg" placeholder="" aria-label="Pinterest" defaultValue="http://www.pinterest.com" />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-xxl-6">
                    <div className="bg-secondary-soft px-4 py-5 rounded">
                      <div className="row g-3">
                        <h4 className="my-4">Change Password</h4>
                        <div className="col-md-6">
                          <label htmlFor="exampleInputPassword1" className="form-label">Old password *</label>
                          <input type="password" className="shadow form-control form-control-lg" id="exampleInputPassword1" />
                        </div>
                        <div className="col-md-6">
                          <label htmlFor="exampleInputPassword2" className="form-label">New password *</label>
                          <input type="password" className="shadow form-control form-control-lg" id="exampleInputPassword2" />
                        </div>
                        <div className="col-md-12">
                          <label htmlFor="exampleInputPassword3" className="form-label">Confirm Password *</label>
                          <input type="password" className="shadow form-control form-control-lg" id="exampleInputPassword3" />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="gap-3 d-md-flex justify-content-md-end text-center">
                  <button type="button" className="btn btn-danger btn-lg">Delete profile</button>
                  <button
      type="button"
      className="btn btn-primary btn-lg"
      onClick={updateProfileInfo} // Updated function name to avoid conflicts
    >
      Update profile
    </button>            </div>
              </form>
          
          </div>
        </div>
      </div>
    </div>
  );
}

export default UserProfile;